.{
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
}

.App{
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
  flex-direction: row;

}

.Menu{
  width: 20vw;
  height: 100vh;
  background-color: black;

  display: flex;
}

.menuContent{
  width: 20vw;
  height: 100%;
  color: white;
  font-size: 0.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.Content{
  width: 80vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.inside{
  width: 60vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  padding-left: 10vw;
  padding-right: 10vw;
  font-size: 0.5em;
  overflow: hidden;
}


.start{
  align-items: flex-start;
  justify-content: flex-start;
}

.center{
  align-items: center;
  justify-content: center;
}

